import React, { useState } from 'react';
import './App.css';
import header from './img/header.png'
import Form from './components/form/form';
import footer from './img/footer.png'
import AnchorLink from 'react-anchor-link-smooth-scroll'


function App() {

  const [login, setLogin] = useState(false);
  const [cadastro, setCadastro] = useState(true);


  const onClickLogin = (el) => {
      setLogin(true);
      setCadastro(false);
  }

  const onClickCadastro = (el) => {
    setCadastro(true);
    setLogin(false);
}

  return (
    <div className="App">
      <header className="App-header">
        <img src={header} className="App-header" alt="logo" />
        <AnchorLink onClick={onClickLogin} offset={() => 100} href='#form' className="linkLogin"></AnchorLink>
        <AnchorLink onClick={onClickCadastro} offset={() => 100} href='#form' className="linkCadastro"></AnchorLink>
      </header>
      <section id='form'>
        <Form onClickCadastro={onClickCadastro} onClickLogin={onClickLogin} cadastro={cadastro} login={login}  />
      </section>
      <footer>
        <img style={{ width: '100%' }} src={footer} />
      </footer>
    </div>
  );
}

export default App;
