import React, { useState } from 'react';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import axios from 'axios';
import formatCpf from '@brazilian-utils/format-cpf';
import validator from 'validator';
import Modal from '@material-ui/core/Modal';

function Register(props) {
    const { classes } = props;
    const [name, setName] = useState("");
    const [cpf, setCpf] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPass, setConfirm] = useState('');
    const [company, setCompany] = useState("");
    const [email, setEmail] = useState("");
    const [classification, setClassification] = useState("");
    const [open, setOpen] = useState(false);

    const handleChange = (event) => {
        setClassification(event.target.value);
    };

    const onChangeName = (event) => {
        setName(event.target.value);
    }

    const onChangeCpf = (event) => {
        setCpf(formatCpf(event.target.value));
    }

    const onChangePassword = (event) => {
        setPassword(event.target.value);
    }

    const onChangeConfirm = (event) => {
        setConfirm(event.target.value);
    }

    const onChangeCompany = (event) => {
        setCompany(event.target.value);
    }

    const onChangeEmail = (event) => {
        setEmail(event.target.value);
    }

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const body = (
        <div style={{
            top: `37%`,
            left: `37%`,
            position: 'absolute',
            width: 400,
            backgroundColor: '#f1b91c',
            borderStyle: 'none',
            padding: 24,
        }} className={classes.paper}>
            <p style={{
                top: "-8%",
                left: "94%",
                fontSize: "19px",
                color: '#f2f2f2'
            }}>X</p>
            <h2 style={{ textAlign: 'center', color: '#f2f2f2' }} id="simple-modal-title">Cadastro realizado com sucesso!</h2>
        </div >
    );

    const submitForm = (event) => {
        axios.post('https://virtserver.swaggerhub.com/ateliedepropaganda/costumer', {
            name: name,
            cpf: cpf,
            email: email,
            password: password,
            company: company,
            classification: classification
        })
            .then(function (response) {
                handleOpen();
                setName('')
                setPassword('')
                setCompany('')
                setConfirm('')
                setCpf('')
                setClassification('')
                setEmail('')
            })
            .catch(function (error) {
                //API está sempre dando erro, mesmo retornando 200 vem sempre um ERR_error antes
                handleOpen();
                setName('')
                setPassword('')
                setCompany('')
                setConfirm('')
                setCpf('')
                setClassification('')
                setEmail('')
            });

        event.preventDefault();
    }

    return (
        <form className={classes.root} autoComplete="off">
            <p className={classes.text1}>Cadastre-se</p>
            <p className={classes.text2}>Preencha os campos abaixo para validar sua participação na campanha e concorrer aos prêmios.</p>
            <TextField onChange={onChangeName} value={name} className={classes.nameInput} style={{ width: '52.6em', height: '9.7ch' }} label="Nome" />
            <TextField onChange={onChangeCpf} value={cpf} label="CPF" />
            <TextField helperText={email ? (!validator.isEmail(email) ? "E-email inválido" : '') : ''} onChange={onChangeEmail} error={email ? !validator.isEmail(email) : false}
                value={email} label="E-mail" />
            <TextField onChange={onChangePassword} value={password} type={'password'} label="Digite sua senha" />
            <TextField onChange={onChangeConfirm} helperText={confirmPass ? (confirmPass !== password ? "Senhas não são iguais" : '') : ''} error={confirmPass ? (confirmPass === password ? false : true) : false} value={confirmPass} type={'password'} label="Confime sua senha" />
            <TextField onChange={onChangeCompany} value={company} label="Empresa" />
            <FormControl style={{ top: 8 }}>
                <InputLabel shrink id="1">Classificação</InputLabel>
                <Select displayEmpty onChange={handleChange} labelId="label" id="select" value={classification} className={classes.select}>
                    <MenuItem value={''}>Escolha sua categoria</MenuItem>
                    <MenuItem value={'Classificação 1'}>Classificação 1</MenuItem>
                    <MenuItem value={'Classificação 2'}>Classificação 2</MenuItem>
                    <MenuItem value={'Classificação 3'}>Classificação 3</MenuItem>
                </Select>
            </FormControl>
            <button onClick={submitForm} className={classes.button}>ENVIAR</button>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
                {body}
            </Modal>
        </form>
    );
}

export default Register;